<template lang="pug">
.modal-dialog
  .modal-content.border-0
    .modal-header.bg-danger
      h5#delProductModalLabel.modal-title(v-if="temp.category")
        span.text-light 
      h5#delOrderModalLabel.modal-title(v-if="temp.create_at")
        span.text-light 
      h5#delCouponLabel.modal-title(v-if="temp.code")
        span.text-light 
      h5#delCouponLabel.modal-title(v-if="temp.qty")
        span.text-light 
      button.btn-close(type="button" data-bs-dismiss="modal")
    .modal-body(v-if="temp.category") 
      strong.text-danger  {{ temp.title }}
      |  ()
    .modal-body(v-if="temp.create_at") 
      strong.text-danger  {{ temp.create_at }}
      |  ()
    .modal-body(v-if="temp.code") 
      strong.text-danger  {{ temp.title }}
      |  ()
    .modal-body(v-if="temp.qty") 
      strong.text-danger  {{ temp.product.title }}
      |  ()
    .modal-footer
      button.btn.btn-outline-secondary(type="button" data-bs-dismiss="modal") 
      button.btn.btn-danger(type="button" v-if="temp.category" @click="delProduct(temp.id)" :disabled="disable === temp.id") 
      button.btn.btn-danger(type="button" v-if="temp.create_at" @click="delOrder(temp.id)" :disabled="disable === temp.id") 
      button.btn.btn-danger(type="button" v-if="temp.code" @click="delCoupon(temp.id)" :disabled="disable === temp.id") 
      button.btn.btn-danger(type="button" v-if="temp.qty" @click="delCart(temp.id)" :disabled="disable === temp.id") 
</template>

<script>
import Swal from 'sweetalert2'

export default {
  props: ['temp', 'delModel'],
  data () {
    return {
      disable: ''
    }
  },
  methods: {
    // 
    delProduct (id) {
      this.disable = id
      this.axios
        .delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${id}`)
        .then(res => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 220
          })
          Toast.fire({
            icon: 'success',
            title: res.data.message
          })
          this.disable = ''
          this.delModel.hide()
          this.$emit('getProducts')
        })
        .catch(error => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 320
          })
          Toast.fire({
            icon: 'error',
            title: error.response.data.message
          })
          this.disable = ''
        })
    },
    // 
    delOrder (id) {
      this.disable = id
      this.axios.delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/order/${id}`)
        .then(res => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 220
          })
          Toast.fire({
            icon: 'success',
            title: res.data.message
          })
          this.disable = ''
          this.delModel.hide()
          this.$emit('getOrders')
        })
        .catch(error => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 320
          })
          Toast.fire({
            icon: 'error',
            title: error.response.data.message
          })
          this.disable = ''
        })
    },
    // 
    delCoupon (id) {
      this.disable = id
      this.axios.delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/coupon/${id}`)
        .then(res => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 240
          })
          Toast.fire({
            icon: 'success',
            title: res.data.message
          })
          this.disable = ''
          this.delModel.hide()
          this.$emit('getCoupons')
        })
        .catch(error => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 320
          })
          Toast.fire({
            icon: 'error',
            title: error.response.data.message
          })
          this.disable = ''
        })
    },
    // 
    delCart (id) {
      this.disable = id
      this.axios.delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${id}`)
        .then(res => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 240
          })
          Toast.fire({
            icon: 'success',
            title: res.data.message
          })
          this.disable = ''
          this.delModel.hide()
          this.$emit('getCarts')
        })
        .catch(error => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 320
          })
          Toast.fire({
            icon: 'error',
            title: error.response.data.message
          })
          this.disable = ''
        })
    }
  }
}
</script>
